import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import GetChannelName from "../../../library/api/GetChannelName";
import "../../../asset/css/Header.css";
import { useSelector } from "react-redux";

const Header = () => {
  const [myCnName, setMyCnName] = useState("");
  const token = useSelector((state) => state.token.value);

  useEffect(() => {
    if (token.accessToken === "") return;
    
    GetChannelName({ nickname: token.nickname })
      .then((res) => setMyCnName(res.data))
      .catch((err) => {
        console.error("     :", err);
        setMyCnName("");
      });
  }, [token]);

  return (
    <header className="header">
      <div className="header-inner">
        <Link to="/" className="logo">
           OnAir
        </Link>

        <nav className="nav">
          {!!token.accessToken ? (
            <>
              {myCnName ? (
                <Link to={`/broadcast/${myCnName}`}></Link>
              ) : (
                <Link to="/channel/join"> </Link>
              )}
              <Link to="/live-list"></Link>
              <Link to="/logout"></Link>
            </>
          ) : (
            <>
              <Link to="/login"></Link>
              <Link to="/join"></Link>
            </>
          )}
        </nav>
      </div>
    </header>
  );
};

export default Header;
